import axios from 'axios';

axios.defaults.baseURL = 'https://api.themoviedb.org/3';
axios.defaults.params = {
  api_key: '53d5445e8f959aee965448ea492bd06d',
  include_adults: false,
};

export const fetchTrending = async () => {
  return axios
    .get(`/trending/all/day?`)
    .then(({ data: { results } }) =>
      results.map(({ id, title, poster_path }) => ({
        id,
        title,
        poster: poster_path,
      }))
    )
    .catch(error => {
      alert(error.massage);
    });
};

export const fetchByQuery = async query => {
  return axios
    .get(`/search/movie?query=${query}`)
    .then(({ data: { results } }) =>
      results.map(({ id, title, poster_path: poster }) => ({
        id,
        title,
        poster,
      }))
    )
    .catch(error => {
      alert(error.massage);
    });
};

export const fetchMovieDetails = async movieId => {
  try {
    const {
      data: {
        id,
        poster_path: poster,
        title,
        release_date: releaseYear,
        vote_average: userScore,
        overview,
        genres,
      },
    } = await axios.get(`/movie/${movieId}?`);
    return {
      id,
      poster,
      title,
      releaseYear: new Date(releaseYear).getFullYear(),
      userScore: Math.round(userScore * 10),
      overview,
      genres,
    };
  } catch (error) {
    console.log(error.message);
  }
};
